<template>
  <div id="tf">
    <common-header :show="true"></common-header>
    <div class="header-top">
      <div class="header-logo">
        <img class="logo" src="data:image/png;base64,">
      </div>
      <router-link to="/Search" class="header-search-wrapper" tag="div">
        <i class="iconfont icon-search">&#xe86e;</i>
          搜索商品名称
      </router-link>
      <div class="header-user">
        <i class="iconfont icon-user">&#xe8a0;</i>
      </div>
    </div>
    <home-header-nav :nav="nav"></home-header-nav>
  </div>
</template>

<script>
import CommonHeader from '@/common/Header/CommonHeader'
import HomeHeaderNav from './nav'

export default {
  components: {
    CommonHeader,
    HomeHeaderNav
  },
  props: {
    nav: Array
  }
}
</script>

<style lang='less' src="@/assets/styles/Home/header.less">
</style>



